body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 24px;
  font-family: "OpenSans", "Arial", sans-serif;
  color: #283645;
  font-style: normal;
  font-weight: normal;
  background-color: #ffffff;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.site-list {
  font-weight: bold;
}
.site-list a {
  display: block;
  padding: 16px;
  padding-bottom: 16px;
  font-size: 18px;
  line-height: 30px;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #283645;
  border-top: 1px solid #485461;
  border-bottom: 1px solid #000000;
}
.site-list__item--active {
  box-shadow: 0 -1px 0 0 #000000;
}
.site-list a[href] {
  margin-top: 0;
}
.site-list a[href]:hover {
  background-color: #283645;
  border-top-color: #283645;
  color: #d22856;
}
.site-list a[href]:active {
  color: rgba(210, 40, 86, 0.33);
}
.site-list li:first-child {
  margin-top: 70px;
}
@media (min-width: 660px) {
  .site-list li:first-child {
    margin-top: 109px;
  }
}
@media (min-width: 1200px) {
  .site-list li:first-child {
    margin-top: 0;
  }
  .main-nav__list.site-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 622px;
    margin-left: 448px;
  }
  .site-list__item--active {
    position: relative;
  }
  .site-list__item--active::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -8px;
    left: 0;
    height: 3px;
    background: #ffffff;
  }
  .site-list__item a {
    margin: 0;
    padding: 0;
    padding-top: 45px;
    background: none;
    border: none;
    font-size: 18px;
    line-height: 18px;
    text-transform: none;
    font-weight: 300;
  }
  .site-list__item a[href]:hover {
    color: #d22856;
    background-color: transparent;
    border-top-color: transparent;
  }
  .site-list__item a[href]:active {
    color: rgba(210, 40, 86, 0.31);
  }
}
