.form h2 {
  margin: 0 auto;
  background-color: white;
  font-size: 14px;
  line-height: 30px;
  font-weight: 300;
  padding: 44px 20px;
  padding-bottom: 48px;
}
.form__group {
  border: none;
  text-align: left;
  width: 280px;
}
.form__acivements,
.form__app {
  background-color: #f2f2f2;
  padding-top: 40px;
}
.form__group legend {
  font-size: 14px;
  line-height: 30px;
  color: #d22856;
  text-transform: uppercase;
}
.form__group label {
  font-size: 14px;
  line-height: 30px;
  color: #d22856;
  text-transform: uppercase;
  padding-left: 20px;
}
.form__acievements label,
.form__app label {
  color: #283645;
  text-transform: lowercase;
}
.form__group input {
  width: 280px;
  height: 64px;
  padding: 0px;
  padding-left: 14px;
  font-size: 18px;
}
.form__checkbox-group,
.form__radio-group {
  margin: 0;
  padding: 0;
  list-style: none;
}
.form__option label {
  position: relative;
  display: block;
  cursor: pointer;
  user-select: none;
  margin-left: 40px;
}
.form__checkbox + label::before {
  content: "";
  position: absolute;
  left: -40px;
  top: 0;
  width: 16px;
  height: 16px;
  border: 2px solid #000000;
}
.form__checkbox:checked + label::after {
  content: "";
  position: absolute;
  width: 12px;
  height: 12px;
  background-color: green;
  top: 4px;
  left: -38px;
  background-image: url("img/cross.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
}
.form__radio + label::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  left: -42px;
  top: -1px;
  border: 2px solid #000000;
  border-radius: 50%;
}
.form__radio:checked + label::after {
  content: "";
  position: absolute;
  top: 5px;
  left: -36px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #000000;
}
